@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Roboto&family=Rum+Raisin&display=swap");

@layer base {
  html {
    font-family: "Roboto", sans-serif;
    color: #1e1e1e;
  }

  body::-webkit-scrollbar {
    width: 8px;
  }

  body::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 5px;
  }

  body::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 5px;
  }

  body::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
  .hidden-animation {
    opacity: 0;
    transition: all 1s;
    filter: blur(5px);
    margin-left: -100%;
  }

  .show-animation {
    opacity: 1;
    filter: blur(0);
    transform: translateX(0);
    margin-left: 0;
  }
}
